import { modelManager, GenerationOptions } from './model-manager';
import { promises as fs } from 'fs';
import { join, resolve, dirname } from 'path';
import { fileURLToPath } from 'url';

export interface ImageOptions extends GenerationOptions {
  style?: 'photorealistic' | 'artistic' | 'anime' | 'concept' | 'technical';
  quality?: 'low' | 'medium' | 'high';
  batch?: number;
  variations?: number;
  size?: '512x512' | '768x768' | '1024x1024' | '1024x768' | '768x1024';
}

export interface ImageResult {
  success: boolean;
  outputPaths?: string[];
  error?: string;
  metadata?: {
    resolution: string;
    style: string;
    modelUsed: string;
    generationTime: number;
    seedUsed: number;
    batchSize: number;
  };
}

export interface ImageProgress {
  stage: 'initializing' | 'processing' | 'finalizing' | 'complete' | 'error';
  percentage: number;
  estimatedTimeRemaining: string;
  currentStep: string;
  currentImage?: number;
  totalImages?: number;
  error?: string;
}

export class ImageGenerationService {
  private outputDir: string;
  private tempDir: string;
  
  constructor() {
    const __filename = fileURLToPath(import.meta.url);
    const __dirname = dirname(__filename);
    const mariaRoot = resolve(__dirname, '../../../../..');
    this.outputDir = join(mariaRoot, 'outputs/images');
    this.tempDir = join(mariaRoot, 'outputs/temp');
  }

  /**
   * ÁîªÂÉèÁîüÊàêÔºàText-to-ImageÔºâ
   */
  async generateImage(
    prompt: string, 
    options: Partial<ImageOptions> = {},
    onProgress?: (progress: ImageProgress) => void
  ): Promise<ImageResult> {
    try {
      const startTime = Date.now();
      
      // ÂàùÊúüÂåñ
      onProgress?.({
        stage: 'initializing',
        percentage: 0,
        estimatedTimeRemaining: 'Ë®àÁÆó‰∏≠...',
        currentStep: 'Qwen-Image„É¢„Éá„É´Á¢∫Ë™ç',
        currentImage: 1,
        totalImages: options.batch || 1
      });

      // ComfyUIËµ∑ÂãïÁ¢∫Ë™ç
      const isComfyUIRunning = await modelManager.checkComfyUIStatus();
      if (!isComfyUIRunning) {
        onProgress?.({
          stage: 'initializing',
          percentage: 10,
          estimatedTimeRemaining: '30Áßí',
          currentStep: 'ComfyUIËµ∑Âãï‰∏≠',
          currentImage: 1,
          totalImages: options.batch || 1
        });
        
        const started = await modelManager.startComfyUI();
        if (!started) {
          throw new Error('ComfyUI„ÅÆËµ∑Âãï„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
        }
      }

      // Qwen-Image„É¢„Éá„É´Á¢∫Ë™ç
      onProgress?.({
        stage: 'initializing',
        percentage: 20,
        estimatedTimeRemaining: this.estimateGenerationTime(options.batch || 1),
        currentStep: 'Qwen-Image„É¢„Éá„É´Ë™≠„ÅøËæº„Åø',
        currentImage: 1,
        totalImages: options.batch || 1
      });

      const modelInfo = await modelManager.getModelInfo('qwen-image');
      if (!modelInfo || modelInfo.status !== 'available') {
        throw new Error('Qwen-Image„É¢„Éá„É´„ÅåÂà©Áî®„Åß„Åç„Åæ„Åõ„Çì');
      }

      // Âá∫Âäõ„Éá„Ç£„É¨„ÇØ„Éà„É™Ê∫ñÂÇô
      await this.ensureDirectories();

      // „Éê„ÉÉ„ÉÅÁîüÊàê„Åæ„Åü„ÅØ„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ÁîüÊàê
      const results = await this.generateBatch(prompt, options, onProgress);
      
      // ÊúÄÁµÇ‰ΩøÁî®Êó•ÊôÇÊõ¥Êñ∞
      await modelManager.updateLastUsed('qwen-image');

      onProgress?.({
        stage: 'complete',
        percentage: 100,
        estimatedTimeRemaining: 'ÂÆå‰∫Ü',
        currentStep: 'ÁîªÂÉèÁîüÊàêÂÆå‰∫Ü',
        currentImage: results.length,
        totalImages: results.length
      });

      // „É°„Çø„Éá„Éº„ÇøÁîüÊàê
      const metadata = this.generateMetadata(
        options,
        Date.now() - startTime,
        results.length
      );

      return {
        success: true,
        outputPaths: results,
        metadata
      };

    } catch (error) {
      onProgress?.({
        stage: 'error',
        percentage: 0,
        estimatedTimeRemaining: '',
        currentStep: '„Ç®„É©„ÉºÁô∫Áîü',
        error: error instanceof Error ? error.message : String(error)
      });

      return {
        success: false,
        error: error instanceof Error ? error.message : String(error)
      };
    }
  }

  /**
   * „Éê„ÉÉ„ÉÅÁîªÂÉèÁîüÊàê
   */
  private async generateBatch(
    prompt: string,
    options: Partial<ImageOptions>,
    onProgress?: (progress: ImageProgress) => void
  ): Promise<string[]> {
    const batchSize = options.batch || 1;
    const variations = options.variations || 1;
    const totalImages = batchSize * variations;
    const results: string[] = [];

    let currentImageIndex = 0;

    // „Éê„É™„Ç®„Éº„Ç∑„Éß„É≥Áî®„Éó„É≠„É≥„Éó„ÉàÁîüÊàê
    const prompts = this.generatePromptVariations(prompt, variations);

    for (let batchIndex = 0; batchIndex < batchSize; batchIndex++) {
      for (let variationIndex = 0; variationIndex < variations; variationIndex++) {
        currentImageIndex++;
        const currentPrompt = prompts[variationIndex] || prompt;
        
        onProgress?.({
          stage: 'processing',
          percentage: Math.min(90, 30 + (currentImageIndex / totalImages) * 60),
          estimatedTimeRemaining: this.estimateRemainingTime(
            currentImageIndex, 
            totalImages, 
            Date.now()
          ),
          currentStep: `ÁîªÂÉèÁîüÊàê‰∏≠ (${currentImageIndex}/${totalImages})`,
          currentImage: currentImageIndex,
          totalImages
        });

        // Âçò‰∏ÄÁîªÂÉèÁîüÊàê
        const imagePath = await this.generateSingleImage(
          currentPrompt,
          options,
          batchIndex,
          variationIndex
        );
        
        results.push(imagePath);

        // Áü≠„ÅÑÂæÖÊ©üÔºàAPIÂà∂ÈôêÂõûÈÅøÔºâ
        if (currentImageIndex < totalImages) {
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
      }
    }

    return results;
  }

  /**
   * Âçò‰∏ÄÁîªÂÉèÁîüÊàê
   */
  private async generateSingleImage(
    prompt: string,
    options: Partial<ImageOptions>,
    batchIndex: number,
    variationIndex: number
  ): Promise<string> {
    // Âá∫Âäõ„Éï„Ç°„Ç§„É´ÂêçÁîüÊàê
    const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
    const filename = `maria_image_${timestamp}_${batchIndex}_${variationIndex}.png`;
    const outputPath = join(this.outputDir, filename);

    // „ÉØ„Éº„ÇØ„Éï„É≠„ÉºË™≠„ÅøËæº„Åø
    const workflowData = await modelManager.loadWorkflow('qwen_image');
    
    // „Éë„É©„É°„Éº„ÇøË®≠ÂÆö
    const size = this.parseImageSize(options.size);
    const generationParams: GenerationOptions = {
      model: 'qwen-image',
      prompt,
      width: size.width,
      height: size.height,
      steps: options.steps || 30,
      guidance: options.guidance || 7.5,
      seed: options.seed || Math.floor(Math.random() * 1000000),
      style: options.style || 'photorealistic',
      outputPath: filename.replace('.png', '')
    };

    // „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÂÆüË°å
    const processedWorkflow = modelManager.replaceWorkflowParameters(
      workflowData.workflow,
      generationParams
    );

    const promptId = await modelManager.executeWorkflow(processedWorkflow);

    // ÁîüÊàêÂÆå‰∫ÜÂæÖÊ©ü
    await this.waitForImageCompletion(promptId);

    // ComfyUIÂá∫Âäõ„Åã„Çâ„Éï„Ç°„Ç§„É´ÁßªÂãï
    await this.moveImageOutput(outputPath);

    return outputPath;
  }

  /**
   * „Éó„É≠„É≥„Éó„Éà„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ÁîüÊàê
   */
  private generatePromptVariations(basePrompt: string, count: number): string[] {
    if (count === 1) {
      return [basePrompt];
    }

    const variations: string[] = [basePrompt];
    
    // Âü∫Êú¨ÁöÑ„Å™„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ÁîüÊàê„É≠„Ç∏„ÉÉ„ÇØ
    const modifiers = [
      'highly detailed',
      'masterpiece quality',
      'professional lighting',
      'vibrant colors',
      'sharp focus',
      'cinematic composition',
      'award winning',
      'trending on artstation'
    ];

    for (let i = 1; i < count; i++) {
      const modifier = modifiers[i % modifiers.length];
      variations.push(`${basePrompt}, ${modifier}`);
    }

    return variations;
  }

  /**
   * „Çπ„Çø„Ç§„É´ÈÅ©Áî®
   */
  async applyStyle(
    prompt: string,
    style: ImageOptions['style'],
    options: Partial<Omit<ImageOptions, 'style'>> = {}
  ): Promise<ImageResult> {
    const stylePrompts = this.getStylePrompt(style || 'photorealistic');
    const enhancedPrompt = `${prompt}, ${stylePrompts}`;
    
    return await this.generateImage(enhancedPrompt, {
      ...options,
      style
    });
  }

  /**
   * Ë§áÊï∞„Çπ„Çø„Ç§„É´ÊØîËºÉÁîüÊàê
   */
  async generateStyleComparison(
    prompt: string,
    styles: ImageOptions['style'][],
    options: Partial<Omit<ImageOptions, 'style'>> = {}
  ): Promise<{ [style: string]: ImageResult }> {
    const results: { [style: string]: ImageResult } = {};
    
    for (const style of styles) {
      if (style) {
        console.log(`üé® ${style} „Çπ„Çø„Ç§„É´„ÅßÁîüÊàê‰∏≠...`);
        results[style] = await this.applyStyle(prompt, style, options);
      }
    }
    
    return results;
  }

  /**
   * ÁîªÂÉè„Ç¢„ÉÉ„Éó„Çπ„Ç±„Éº„É´ÔºàÂ∞ÜÊù•ÂÆüË£ÖÔºâ
   */
  async upscaleImage(
    _imagePath: string,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    _scaleFactor: number = 2
  ): Promise<ImageResult> {
    // TODO: Real-ESRGAN„ÇÑWAIFU2XÁµ±Âêà
    return {
      success: false,
      error: '„Ç¢„ÉÉ„Éó„Çπ„Ç±„Éº„É´Ê©üËÉΩ„ÅØÊú™ÂÆüË£Ö„Åß„Åô'
    };
  }

  /**
   * ÁîªÂÉè„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ÁîüÊàêÔºàÂ∞ÜÊù•ÂÆüË£ÖÔºâ
   */
  async generateVariations(
    _imagePath: string,
    _prompt: string,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    _count: number = 4
  ): Promise<ImageResult> {
    // TODO: Image-to-Image variation„ÅÆÂÆüË£Ö
    return {
      success: false,
      error: '„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ÁîüÊàêÊ©üËÉΩ„ÅØÊú™ÂÆüË£Ö„Åß„Åô'
    };
  }

  /**
   * „Éó„É©„Ç§„Éô„Éº„Éà„É°„ÇΩ„ÉÉ„Éâ
   */
  private async waitForImageCompletion(promptId: string): Promise<void> {
    const maxWaitTime = 5 * 60 * 1000; // 5ÂàÜ„Çø„Ç§„É†„Ç¢„Ç¶„Éà
    const checkInterval = 2000; // 2ÁßíÈñìÈöî
    const startTime = Date.now();

    while (Date.now() - startTime < maxWaitTime) {
      const status = await modelManager.checkProgress(promptId);
      
      if (status.completed) {
        return;
      }
      
      if (status.error) {
        throw new Error(`ÁîªÂÉèÁîüÊàê„Ç®„É©„Éº: ${status.error}`);
      }

      await new Promise(resolve => setTimeout(resolve, checkInterval));
    }

    throw new Error('ÁîªÂÉèÁîüÊàê„Åå„Çø„Ç§„É†„Ç¢„Ç¶„Éà„Åó„Åæ„Åó„Åü');
  }

  private async moveImageOutput(targetPath: string): Promise<void> {
    // TODO: ComfyUI„ÅÆÂá∫Âäõ„Éá„Ç£„É¨„ÇØ„Éà„É™„Åã„ÇâtargetPath„Å∏„Éï„Ç°„Ç§„É´ÁßªÂãï
    // ÁèæÂú®„ÅØ„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„ÉºÂÆüË£Ö
    console.log(`ÁîªÂÉèÂá∫Âäõ„Çí ${targetPath} „Å´ÁßªÂãïÊ∫ñÂÇô‰∏≠...`);
  }

  private parseImageSize(size?: string): { width: number; height: number } {
    switch (size) {
      case '512x512':
        return { width: 512, height: 512 };
      case '768x768':
        return { width: 768, height: 768 };
      case '1024x1024':
        return { width: 1024, height: 1024 };
      case '1024x768':
        return { width: 1024, height: 768 };
      case '768x1024':
        return { width: 768, height: 1024 };
      default:
        return { width: 1024, height: 1024 };
    }
  }

  private getStylePrompt(style: ImageOptions['style']): string {
    switch (style) {
      case 'photorealistic':
        return 'photorealistic, high resolution, detailed, professional photography';
      case 'artistic':
        return 'artistic, painterly style, creative, expressive, fine art';
      case 'anime':
        return 'anime style, manga, cel shading, vibrant colors, Japanese art';
      case 'concept':
        return 'concept art, digital painting, matte painting, cinematic';
      case 'technical':
        return 'technical illustration, clean lines, precise, schematic style';
      default:
        return 'high quality, detailed';
    }
  }

  private estimateGenerationTime(batchSize: number): string {
    const timePerImage = 45; // Áßí
    const totalSeconds = batchSize * timePerImage;
    
    if (totalSeconds < 60) {
      return `${totalSeconds}Áßí`;
    } else {
      return `${Math.ceil(totalSeconds / 60)}ÂàÜ`;
    }
  }

  private estimateRemainingTime(
    currentImage: number,
    totalImages: number,
    startTime: number
  ): string {
    const elapsed = Date.now() - startTime;
    const avgTimePerImage = elapsed / currentImage;
    const remainingImages = totalImages - currentImage;
    const remainingMs = remainingImages * avgTimePerImage;

    if (remainingMs < 60000) {
      return `${Math.ceil(remainingMs / 1000)}Áßí`;
    } else {
      return `${Math.ceil(remainingMs / 60000)}ÂàÜ`;
    }
  }

  private generateMetadata(
    options: Partial<ImageOptions>,
    generationTime: number,
    imageCount: number
  ): ImageResult['metadata'] {
    const size = this.parseImageSize(options.size);
    
    return {
      resolution: `${size.width}x${size.height}`,
      style: options.style || 'photorealistic',
      modelUsed: 'qwen-image',
      generationTime: Math.round(generationTime / 1000),
      seedUsed: options.seed || -1,
      batchSize: imageCount
    };
  }

  private async ensureDirectories(): Promise<void> {
    await fs.mkdir(this.outputDir, { recursive: true });
    await fs.mkdir(this.tempDir, { recursive: true });
  }
}

